.titleContent {
    font-family: 'NoyhABistro-Rough';
    font-style: normal;
    font-weight: 500;
    font-size: 34px;
    line-height: 39px;
    position: absolute;
    color: #AE875A;
    padding-top: 8%;
}

.tempRow5 {
    padding-left: 120px;
    width: 100%;
    display: flex;
    justify-content: center;
}

@media only screen and (max-width: 992px) {
    .tempRow5 {
        padding-left: 0
    }
}

.tempColumn5 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1440px;
}

.foto1 {
    position: absolute;
    padding-top: 15%;
}

.foto2 {
    position: absolute;
    padding-left: 50%;
    padding-top: 5%;
}

.kontaktsContent .name {
    font-family: 'Inter';
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    color: #33200B;
}

.kontaktsContent {
    padding-left: 20px;
}

.kontaktsContent h2 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    color: #AE875A;
    padding-top: 3px;
}

.ikons {
    color: #AE875A;
    padding-top: 39px;
}

.info {
    font-family: 'Inter';
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #33200B;
    padding-top: 4px;
    margin-bottom: 0;
    text-decoration: none;
}

.ikons a {
    color: #AE875A;
    padding-top: 39px;
}

.info a {
    color: #33200B;
    text-decoration: none;
}

.tempRow6 {
    position: absolute;
    margin-top: 57%;
    width: 1440px;
}

@media screen and (max-width: 992px) {
    .kontaktsContent {
        padding-top: 35px;
    }

    .titleContent {
        margin-left: 35%;
    }

    .foto1 {
        width: 280px;
        padding-top: 46%;
        margin-left: 90px;
        padding-left: 30px;
    }

    .foto2 {
        width: 320px;
        margin-left: 5%;
        padding-top: 70%;

    }

    .tempRow6 {
        width: 100vw;
        padding-top: 190%;
        padding-left: 15px;
    }
}

@media screen and (min-width: 1366px) {
    .foto2 {
        z-index: 1000;
    }
}